form {
	height: 65vh;
	width: 25em;
	grid-column: 1 / span 3;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	select {
		place-self: center;
		height: 3.75em;
		width: 87%;
		border-radius: 5px;
		border: none;
		box-shadow: 2px 2px 7px rgb(89, 89, 89);
	}
	input {
		box-shadow: 1px 1px 3px rgb(89, 89, 89);
		place-self: center;
		border-radius: 5px;
		border: none;
		height: 3em;
		width: 85%;
	}
}
